import { useEffect } from "react";
import { useForm } from "react-hook-form";
import styled from "styled-components";
import { postJoin } from "../api/auth_api";
import AuthButton from "../components/auth/AuthBotton";
import AuthForm from "../components/auth/AuthForm";
import AuthInput from "../components/auth/AuthInput";

import Container from "../components/Container";
import Error from "../components/Error";

export const JOIN_PATH = "/join";
const Title = styled.h1`
  font-size: 38px;
  font-weight: 700;
`;
export interface IJoinForm {
  email: string;
  password: string;
  password2: string;
  username: string;
  result?: string;
}

const Join = () => {
  const { register, handleSubmit, formState, setError, setFocus } =
    useForm<IJoinForm>({
      // mode: "onBlur",
    });

  useEffect(() => {
    setFocus("email");
  }, [setFocus]);

  const onValid = async (inputs: IJoinForm) => {
    if (inputs.password !== inputs.password2) {
      setError(
        "password",
        { message: "  ." },
        {
          shouldFocus: true,
        }
      );

      return;
    }
    try {
      const data = await postJoin(inputs);
      console.log(data);
    } catch (e: any) {
      setError("email", {
        message: "  .",
        type: "focus",
      });
    }
  };
  return (
    <Container>
      <Title></Title>
      <AuthForm onSubmit={handleSubmit(onValid)}>
        {formState.errors && (
          <>
            {formState.errors.result && (
              <Error message={formState.errors.result?.message} />
            )}
            {formState.errors.email && (
              <Error message={formState.errors.email?.message} />
            )}
            {formState.errors.password && (
              <Error message={formState.errors.password?.message} />
            )}
          </>
        )}
        <AuthInput
          {...register("email", {
            required: " ",
            minLength: {
              value: 6,
              message: " 6   .",
            },
          })}
          placeholder="email"
        />
        <AuthInput
          type={"password"}
          {...register("password", {
            required: " ",
            minLength: {
              value: 4,
              message: " 4   .",
            },
          })}
          placeholder="password"
        />
        <AuthInput
          type={"password"}
          {...register("password2")}
          placeholder="check"
        />
        <AuthInput
          {...register("username", {
            required: " ",
            minLength: {
              value: 4,
              message: " 4   .",
            },
          })}
          placeholder="username"
        />
        <AuthButton></AuthButton>
      </AuthForm>
    </Container>
  );
};

export default Join;
